import { SearchService } from 'src/assets/services/search.service';
import { ConfirmDialogComponent } from './../confirm-dialog/confirm-dialog.component';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { range } from 'rxjs';
import { CarClass } from 'src/assets/enums/carClass';
import { DataSharingService } from 'src/assets/services/data-sharing.service';
import { PostAcceptAgreementDto } from 'src/assets/interfaces/postAcceptAgreementDto';
import { Router } from '@angular/router';
import { SelectInsuranceResult } from 'src/assets/interfaces/selectInsuranceResult';

@Component({
  selector: 'app-booking-details-page',
  templateUrl: './booking-details-page.component.html',
  styleUrls: ['./booking-details-page.component.scss']
})
export class BookingDetailsPageComponent implements OnInit {
  monthArray: number[] = [];
  daysArray: number[] = [];
  yearsArray: number[] = [];
  year!:number;
  totalPrice!:number;
  insurancePrice!:number;
  insurance!: string;
  fromDate: string | null;
  toDate: string | null;
  maxDate: Date;
  firstName: string | undefined;
  lastName: string | undefined;
  email: string | undefined;
  phoneNumber: string | undefined;
  street: string | undefined;
  zipCode: string | undefined;
  city: string | undefined;
  country: string | undefined;
  birthDate:moment.Moment | undefined;
  cardHolder: string | undefined;
  cardNumber: number | undefined;
  expiryMonth:number | undefined;
  expiryYear:number | undefined;
  cvv: number | undefined
  data!: SelectInsuranceResult;

  choosenCar: {
    id: number,
    make: string,
    model: string,
    year: number,
    site: string,
    color: string,
    dailyRentalPrice: number,
    carClass: CarClass, 
    transmissionType: string
  }
  constructor( 
    private dataSharingService: DataSharingService,
    private dialog: MatDialog,
    private searchService:SearchService,
    private router: Router
    ) { 
    this.createMonthArray();
    this.createDaysArray();
    this.createYearsArray();
    this.choosenCar = this.dataSharingService.carSelected;
    this.totalPrice = this.dataSharingService.totalPrice;
    this.insurancePrice = this.dataSharingService.insurancePrice;
    this.insurance = this.dataSharingService.insurance;
    this.fromDate = this.dataSharingService.fromDate;
    this.toDate = this.dataSharingService.toDate;
    this.data = this.dataSharingService.insuranceSelectionResult;
    //set maxDate
    this.maxDate = new Date();
    this.maxDate.setDate( this.maxDate.getDate() );
    this.maxDate.setFullYear( this.maxDate.getFullYear() - 18 );
    
  }

  ngOnInit(): void {
  }

  createMonthArray() {
    let start=1;
    while(start<= 12) {
      this.monthArray.push(start);
      start ++;
    }
  }

  createDaysArray() {
    let start=1;
    while(start<= 31) {
      this.daysArray.push(start);
      start ++;
    }
  }
  createYearsArray() {
    let start= 1980 ;
    while(start<= 2002) {
      this.yearsArray.push(start);
      start ++;
    }
  }

  openConfirmDialog() {
    this.dialog.open(ConfirmDialogComponent);
  }

  cancel() {
    let input: PostAcceptAgreementDto = {
      acceptAgreement: false,
      processInstanceId: this.dataSharingService.processInstanceId
    }
    this.searchService.postAcceptAgreement(input).subscribe(response => {
      if(response.status >= 200 && response.status < 300) {
        if(response.body?.response === "Success") {
          this.router.navigateByUrl("");
        }
      }
    });
  }
  book() {
    let input: PostAcceptAgreementDto = {
      acceptAgreement: true,
      processInstanceId: this.dataSharingService.processInstanceId,
      firstName: this.firstName,
      lastName: this.lastName,
      email: this.email,
      phoneNumber: this.phoneNumber,
      street: this.street,
      zipCode: this.zipCode,
      city: this.city,
      country: this.country,
      birthDate: this.birthDate,
      cardHolder: this.cardHolder,
      cardNumber: this.cardNumber,
      expiryMonth: this.expiryMonth,
      expiryYear: this.expiryYear,
      cvv: this.cvv
    }
    this.searchService.postAcceptAgreement(input).subscribe(response => {
      if(response.status >= 200 && response.status < 300) {
        if(response.body?.response === "Success") {
          this.dialog.open(ConfirmDialogComponent);
        }
      }
    });

    }


  // book() {
  //   this.searchService.postBooking({
  //     acceptAgreement: true,
  //     firstName: this.firstName,
  //     lastName: this.lastName,
  //     email: this.email,
  //     birthDate: this.birthDate
  //   }, this.dataSharingService.processInstanceId).subscribe( data => {

  //   });
  // }

}
