import { EmployeeService } from './../../assets/services/employee.service';
import { DataSharingService } from 'src/assets/services/data-sharing.service';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import {animate, state, style, transition, trigger} from '@angular/animations';
import { Router } from '@angular/router';
import { PostActiveTasksResult } from 'src/assets/interfaces/postActiveTasksResult';
import { PostAcceptAgreementDto } from 'src/assets/interfaces/postAcceptAgreementDto';
