import { DataSharingService } from './../../assets/services/data-sharing.service';
import { LoginDto } from './../../assets/interfaces/loginDto';
import { LoginService } from './../../assets/services/login.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TestRestApiComponent } from '../test-rest-api/test-rest-api.component';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  user!: LoginDto;
  username!: string;
  password!: string;
  hint: boolean = false;
  constructor(
    private loginService: LoginService,
    private router: Router,
    ) {
    if(sessionStorage.length !== 0) {
      this.router.navigateByUrl("employeePage");
    }
  }

  ngOnInit(): void {
  }
  login(uName:string, pWord:string) {
    this.user = {
      username: uName,
      password: pWord 
    }
    this.loginService.login(this.user).subscribe(response => {
      if(response.status == 200) {
        sessionStorage.setItem("currentUser", this.user.username);
        sessionStorage.setItem("token", <string>response.body);
        this.router.navigateByUrl("employeePage");
      } 
    });

    // if(sessionStorage.length === 0) {
    //   setTimeout((): boolean => {
    //     this.hint = true;
    //     return this.hint;
    //   },500);
    // }
  }
}
