import { DataSharingService } from 'src/assets/services/data-sharing.service';
import { SearchResultDto } from 'src/assets/interfaces/searchResultDto';
import { LocationClass } from './../../assets/enums/locations';
import { AfterViewInit, Component, OnInit } from '@angular/core';    
import { Router } from '@angular/router';
import { NgForm } from '@angular/forms';
import { PostSearchDto } from 'src/assets/interfaces/postSearchDto';
import { SearchService } from 'src/assets/services/search.service';
import { DatePipe } from '@angular/common';


export const MY_FORMAT = {
  parse: {
    dateInput: "DD/MM/YYYY"
  },
  display: {
    dateInput: "YYYY-MM-DD",
    monthYearLabel:"YYYY",
    dateA11yLabel: "LL",
    monthA11yLabel: "YYYY"
  }
};

@Component({
  selector: 'app-landing-page',
  templateUrl: './landing-page.component.html',
  styleUrls: ['./landing-page.component.scss']
})
export class LandingPageComponent {
  searchActive: boolean;
  intersectionObserver!: IntersectionObserver;
  site: LocationClass | undefined;
  locationSites!: LocationClass[];
  fromDate: Date | undefined;
  toDate: Date | undefined;
  minFromDate: Date;
  minToDate: Date;
  maxFromDate: Date | undefined;
  older23:number | undefined;
  numberOfDrivers: number| undefined;
  data!: SearchResultDto;
  numberOfCars!:number;
  isLoading: boolean;

  constructor(
    private restSearchService: SearchService, 
    private dataSharingService: DataSharingService,
    public datepipe: DatePipe,
    private router:Router
    ) {
    this.searchActive = false;
    this.locationSites= Object.values(LocationClass);
    this.minFromDate = new Date();
    this.minToDate = new Date();
    this.isLoading =false;
  }

  
  onSearch(input:PostSearchDto): void {
    if(this.searchActive) {
      this.searchActive = false;
    }
    this.isLoading = true;
    this.dataSharingService.fromDate = this.datepipe.transform(this.fromDate, "YYYY-MM-dd" );
    this.dataSharingService.toDate = this.datepipe.transform(this.toDate, "YYYY-MM-dd" );
    this.restSearchService.postSearch(input).subscribe(data => {
      this.searchActive = true;
      this.data = data;
      this.dataSharingService.carSelectionResults = data;
      this.isLoading =false;
    });
  }

  getMaxFromDate(): Date | undefined {
    if(this.toDate == undefined) { 
       return undefined;
     } else {
       return this.maxFromDate = this.toDate;
    }
  }

  getMinToDate(): Date {
   if(this.fromDate == undefined) {
     return this.minToDate;
   } else {
     return this.minToDate = this.fromDate; 
   }
  }


  navigateToCarAccidentPage() {
    this.router.navigateByUrl("carAccidentNotificationPage");
  }

  navigateToEmployeePage() {
    this.router.navigateByUrl("employeePage");
  }
}
