import { DataSharingService } from './../../../assets/services/data-sharing.service';
import { CarClass } from 'src/assets/enums/carClass';
import { SearchResultDto } from 'src/assets/interfaces/searchResultDto';
import { Component, Input, OnInit, Output, AfterViewInit } from '@angular/core';
import { SearchService } from 'src/assets/services/search.service';
import { PostSelectCarDto } from 'src/assets/interfaces/postSelectCarDto';
import { Router } from '@angular/router';

@Component({
  selector: 'app-car-selection-page',
  templateUrl: './car-selection-page.component.html',
  styleUrls: ['./car-selection-page.component.scss']
})
export class CarSelectionPageComponent implements OnInit {
  isLoading= false;
  @Input() results!: SearchResultDto;
  fittingCars!: [{
    id: number,
    make: string,
    model: string,
    year: number,
    site: string,
    color: string,
    dailyRentalPrice: number,
    carClass: CarClass, 
    transmissionType: string
}]
trimmedFittingCars!: [{
  id: number,
  make: string,
  model: string,
  year: number,
  site: string,
  color: string,
  dailyRentalPrice: number,
  carClass: CarClass, 
  transmissionType: string
}];
  constructor(
    private dataSharingService: DataSharingService,
    private searchService: SearchService,
    private router: Router
    ) {
      
  }
  
  ngOnInit(): void {
    this.fittingCars =  this.dataSharingService.carSelectionResults.fittingCars;
    let index = 0;
    while( index < this.fittingCars.length) {
      if (index === 0) {
        this.trimmedFittingCars = [this.fittingCars[index]];
      } else if(this.fittingCars[index - 1].carClass !== this.fittingCars[index].carClass){
        let car: {
          id: number,
          make: string,
          model: string,
          year: number,
          site: string,
          color: string,
          dailyRentalPrice: number,
          carClass: CarClass, 
          transmissionType: string
        };
        let carClassExists = false;
        for (car of this.trimmedFittingCars) {
          if ( car.carClass === this.fittingCars[index].carClass) {
            carClassExists = true;
            break;
          }
        }
        if(!carClassExists) {
          this.trimmedFittingCars.push(this.fittingCars[index]);
        }
      }
      index = index + 1;
    }
    console.log(this.fittingCars);
    console.log(this.trimmedFittingCars);

    this.dataSharingService.processInstanceId = this.dataSharingService.carSelectionResults.processInstanceId;
    // console.log(this.results.fittingCars.id);
  }
  selectCar(r:{
    id: number,
    make: string,
    model: string,
    year: number,
    site: string,
    color: string,
    dailyRentalPrice: number,
    carClass: CarClass, 
    transmissionType: string
}) {
    // console.log(r);
    let input: PostSelectCarDto = {
      carClass: r.carClass,
      processInstanceId: this.dataSharingService.carSelectionResults.processInstanceId
    }
    this.isLoading = true;
    this.searchService.postSelectCar(input).subscribe(response => {
      this.dataSharingService.carSelected = r;
      this.dataSharingService.selectCarResponse = response;
      this.isLoading = false;
      this.router.navigateByUrl("insuranceSelection");
    });
  }

  getSource(input: string):string {
    let source="";
    switch(input) {
      case CarClass.SMALL:
        source = "assets/img/smallCar.png";
        break;

      case CarClass.MEDIUM:
        source = "assets/img/mediumCar.png";
        break;

      case CarClass.SPORTS:
        source = "assets/img/sportCar.png";
        break;
        
      case CarClass.LARGE:
        source = "assets/img/swjCar.png";
        break;

      case CarClass.TRANSPORTER:
        source = "assets/img/bigTransporter.png";
        break;

      case CarClass.SMALL_TRANSPORTER:
        source = "assets/img/smallTransporter.png";
        break;

      case CarClass.BUS:
        source = "assets/img/smallBus.png";
        break;

      case CarClass.MINIVAN:
        source = "assets/img/minivanCar.png";
        break;

      case CarClass.LUXURY:
        source = "assets/img/jaguar.png";
        break;

    }
    return source;

  }
}
