import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { PostIncidentReport } from 'src/assets/interfaces/postIncidentReport';
import { SearchService } from 'src/assets/services/search.service';

@Component({
  selector: 'app-car-accident-notification-page',
  templateUrl: './car-accident-notification-page.component.html',
  styleUrls: ['./car-accident-notification-page.component.scss']
})
export class CarAccidentNotificationPageComponent implements OnInit {
  incidentDate!: moment.Moment;
  licensePlate!: string;
  driverFirstName!: string;
  driverLastName!: string;
  driverAge!: number;
  driverLicense!: string;
  driverCountry!: string;
  driverCity!: string;
  driverZipCode!: string;
  driverStreet!: string;
  driverPhoneNumber!: string;
  driverEmail!: string;
  incidentSummary!: string;
  towingNeeded: boolean =false;
  accidentCity!: string;
  accidentZipCode!: string;
  accidentAddress!: string;
  maxFromDate= new Date();
  isLoading = false;
  constructor(private router:Router, private searchService: SearchService) {
    this.towingNeeded = false;
   }

  ngOnInit(): void {
  }

  navigateHomePage() {
    this.router.navigateByUrl("");
  }

  incidentReport(input:PostIncidentReport) {
    this.isLoading = true;
    this.searchService.postIncidentReport(input).subscribe(response => {
      if(response.status >= 200 && response.status < 300) {
        this.isLoading = false;
        this.router.navigateByUrl("");
      }
    });
  }

}
